@import url(https://fonts.googleapis.com/css?family=Fira+Sans:200i|Noto+Sans|Noto+Sans+TC|PT+Serif&display=swap);
/* default style */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* ============================================== */
/* customized style */

/* texts and fonts */

/* containers */
body, html {
  height: 100%;
  margin: 0;
}

.container-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.row {
  padding-left: 3rem;
  padding-right: 3rem;
}

/* text */
h1 {
  font-family: 'PT Serif', serif;
  font-size: 28px;
}

h1.footer {
  font-family: 'Noto Sans', sans-serif;
  font-size: 14px;
}

h2 {
  color: black;
  font-family: 'PT Serif', serif;
  font-size: 24px;
  line-height: 180%;
  margin-bottom: 1rem;
}

h3 {
  font-size: 20px;
  font-weight: bold;
  /* margin-bottom: 12px; */
  color: gray;

}

h4 {
  font-size: 16px;
  color: gray;
}

h5 {
  font-size: 14px;
  color: gray;
  font-family: 'Noto Sans', sans-serif;
}

h6 {
  font-size: 12px;
}

p {
  font-family: 'PT Serif', serif;
  color: gray;
}

/* section */


/* component - navbar */

.navbar {
  background-color: #ffffff95;
}

.nav-link {
  color: #888888;
  font-size: 16px;
  font-family: 'Noto Sans', sans-serif;
  text-align: center;
  margin: 0 10px;
  border-bottom-style: solid;
  border-width: 3px;
  border-color: transparent;
}

.nav-link:hover,
.nav-link:focus,
.nav-link:active {
  color: #555555;
  border-bottom-style: solid;
  border-width: 3px;
  border-color: #555555;
}

/* component - footer */

footer {
  color: grey;
  background-color: black;
  padding-top: 14px;
  padding-bottom: 14px;
  margin-top: auto;
  text-align: center;
}

a.footer {
  color: lightgray;
  margin-left: 4px;
  margin-right: 4px;
}

a.footer:hover,
a.footer:focus,
a.footer:active {
  color: white;
}

/* component - card */

.card {
  width: 18rem;
}

.card-img {
  opacity: 50%;
}

/* background images */

.img-bg-main,
.img-bg-recent-work,
.img-bg-selected-work,
.img-bg-selected-work-more,
.img-bg-about,
.img-bg-contact {
  height: 100vh;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative
}

.img-bg-main {
  background-image: url(/static/media/elena-joland-mjeQon0Mh_Q-unsplash.1db32416.jpg);
}
.img-bg-recent-work {
  background-image: url(/static/media/bench-accounting-xxeAftHHq6E-unsplash.d3f31a71.jpg);
}
.img-bg-selected-work {
  background-image: url(/static/media/philipp-berndt-5i0GnoTTjSE-unsplash.dc5542bd.jpg);
}
.img-bg-selected-work-more {
  background-image: url(/static/media/bench-accounting-nvzvOPQW0gc-unsplash.ca4263d6.jpg);
}
.img-bg-about {
  background-image: url(/static/media/scott-webb-zblX5hM9rp8-unsplash.39b7e2cd.jpg);
  transform: scaleX(-1);
}
.img-bg-contact {
  background-image: url(/static/media/samantha-gades-BlIhVfXbi9s-unsplash.e62a58d6.jpg);
}

/* opacity adjustment */
.img-bg-main::before,
.img-bg-recent-work::before,
.img-bg-selected-work::before,
.img-bg-selected-work-more::before,
.img-bg-about::before,
.img-bg-contact::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: #ffffff80;
}

#img-profile {
  border-radius: 50%;
  border-style: solid;
  border-width: 6px;
  border-color: #ffffff;
}

